import { ModalController, LoadingController } from "ionic-angular";
import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { FileZipSetViewModel } from "../../Model/ViewModel/FileZipSetViewModel";

/**
 * Generated class for the FileZipSetModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-file-zip-set-add-edit-modal",
  templateUrl: "file-zip-set-add-edit-modal.html"
})
export class FileZipSetAddEditModalPage {
  item: FileZipSetViewModel;
  mode: string;
  CanEditBatch: boolean;
  title: string;
  FileZipSet_type: string;
  change_mode:string;
  src_exec_group:string="";
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public loadingCtrl: LoadingController,
    public modalCtrl: ModalController
  ) {

    this.mode = navParams.data.mode;
    this.change_mode=navParams.data.change_mode;
    this.item = new FileZipSetViewModel();
    this.item.zip_seq=navParams.data.item.zip_seq;
    this.item.src_exec_group=navParams.data.item.src_exec_group;
     this.item.creator=navParams.data.item.creator;
    this.item.create_time=navParams.data.item.create_time;
    this.item.modifier=navParams.data.item.modifier;
    this.item.last_update_time=navParams.data.item.last_update_time;


    this.CanEditBatch = navParams.data.CanEditBatch;

    this.title = "壓縮檔來源設定";
    if(this.mode==="PUT")
      this.src_exec_group="zip_src_"+ this.item.src_exec_group;
    else
      this.src_exec_group="";
  }




  SelectFileZipSrcExecGroup() {
    let modal = this.modalCtrl.create("BatchExecProgramSelectModalPage", {
      ssis_program_set_no:"zip_src",
      select_item: this.src_exec_group,
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;

      this.item.src_exec_group = select_data.exec_group;

    });

    modal.present();
  }


  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad FileZipSetModalPage");
  }
}



